.project-page {
  display: flex;
  justify-content: center;
  align-items: center;
  @media screen and (max-width: 1600px) {
    margin-top: 100px;
  }
  @media screen and (max-width: 768px) {
    margin-top: 0;
  }
  .redirect {
    transition: 0.5s;
    &:hover {
      transform: scale(1.02);
      transition: 0.5s;
    }
  }
  .project-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
    max-width: 1400px;
    margin: 50px auto 0;
    @media screen and (min-width: 1940px) {
      margin: 250px auto 300px;
  }
    @media screen and (max-width: 1600px) {
      flex-direction: column;
      align-items: center;
      margin: 150px auto 50px;
    }
    .project-card {
      background-color: rgba($white, 0.7);
      border-radius: 10px;
      padding: 20px;
      text-align: center;
      color: $black;
      width: 100%;
      height: auto;
      transition: 0.5s;
      .card-title {
        font-size: 1.3rem;
        margin: 5px auto 10px;
      }
      @media screen and (max-width: 768px) {
        display: flex;
        flex-direction: column;
        justify-content: center;
        background-color: rgba($white, 0.6);
        padding: 2px;
      }
      h2 {
        font-size: 36px;
        margin-bottom: 30px;
      }
      p {
        font-size: 24px;
        margin-bottom: 30px;
      }
      img {
        width: 100%;
        height: 250px;
        object-fit: cover;
        border-radius: 10px;
      }
      @media screen and (max-width: 1600px) {
        width: 100%;
        max-width: 600px;
      }
    }
  }
}
