nav {
    position: absolute;
    top: 30px;
    left: 24px;
    font-size: 1.5rem;
    line-height: 55px;
    color: $black;
    z-index: 1;
    a {
      text-decoration: none;
      color: $black;
    }
    li {
      transition: .3s;
      transition-delay: 0.3s;
    }
    .nav-background {
      position: absolute;
      height: 350px;
      width: 350px;
      border-radius: 50%;
      background: $color-1;
      top: -150px;
      left: -160px;
      z-index: -1;
      animation: navAnim 20s infinite alternate;
      @keyframes navAnim {
        50% {
          transform: scale(1.2);
        }
        100% {
          transform: scale(1);
        }
      }
    }    
    .nav-active {
      color: $black;
    }
    ul:hover li:not(:hover) {
      opacity: .5;
    }
    ul:hover li:hover {
      color: $black;
      background-color: $white;
      border-radius: 50%;
      transition: .5s;
      transform: translateX(40px);
    }
  
    .nav-portfolio {
      cursor: pointer;
      display: flex;
      transition: .5s;
      li {
        transform: rotatey(90deg);
      }
  
      &:hover .nav-projects,
      .nav-projects:hover {
        visibility: visible;
        opacity: 1;
  
        li {
          transform: rotatey(0deg);
          transition: 1s;
          &:hover {
            width: auto;
            transition-delay: 0.1s;
          }
        }
      }
      .nav-projects {
        position: absolute;
        visibility: hidden;
        opacity: 0;
        padding: 0 20px;
        width: 400px;
        transform: translateX(60px);
        transition: .3s ;
        background-color: $color-1;
        border-radius: 50%;
        padding: 0 150px;
        li {
          transition: .3s;
          font-size: 1.5rem;
          color: $black;
          text-decoration: none;
        }
      }
    }
    .social-networks {
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: fixed;
      bottom: 10%;
      border: 2px solid $color-2;
      border-radius: 50px;
      padding: 10px 2px 0px;
      img {
        height: auto;
        width: 60px;
        background-color: $black;
        border-radius: 50%;
        border: 2px solid $black;
      }
    }
  }