.lava-lamp {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    overflow: hidden;
  }
  .blob {
    position:absolute;
    border-radius: 50%;
  }
  .blob-orange {
    background-color: $color-1;
    box-shadow: 0 0 20px $color-2;
  }
  .blob-blue {
    background-color: $color-3;
    box-shadow: 0 0 20px $color-4;
  }
  .blob-green {
    background-color: #baffc9;
    box-shadow: 0 0 20px greenyellow; 
  }
  .blob-purple {
    background-color: mediumslateblue;
    box-shadow: 0 0 20px rgb(217, 66, 217); 
  }
  .blob-beige {
    background-color: beige;
    box-shadow: 0 0 20px blue; 
  }