main {
    display: flex;
    flex-direction: column;
    align-items: center;
}

h1 {
    margin: 80px auto 0;
    overflow: hidden;
    span {
        font-size: 3rem;
    }
    @media screen and (max-width: 1600px) {
        margin: 500px auto 50px;
    }
    @media screen and (max-width: 768px) {
        margin: 125px auto 50px;
    }
}
h2 {
    font-size: 1.5rem;
    margin: -20px auto 20px;
}
.blob-photo-container {
    display: flex;
    height: 25vh;
    justify-content: center;
    align-items: center;
    @media screen and (max-width: 768px) {
        height: 30vh;
    }
    .blob-photo {
        user-select: none;
        border-radius: 50%;
        box-shadow: 0 0 20px $color-2;
    }
    .blob-photo img {
        border-radius: 50%;
        object-fit:cover;
        height: 100%;
        width: 100%;
    }
}
h3 {
    font-size: 1.5rem;
    margin: 80px auto 10px;
    @media screen and (min-width: 1940px) {
        margin: 100px auto 0;
    }
}
.skills-container {
    border: 2px solid white;
    border-radius: 50px;
    background-color: rgba($black, 0.95);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 50%;
    padding: 10px 0 40px;
    margin: 0 auto 10px;
    @media screen and (min-width: 1940px) {
        margin: 50px auto 0;
    }
    @media screen and (max-width: 768px) {
        width: 90%;
      }
    .front-container, .back-container, .other-container { 
        display: flex;
        flex-wrap: wrap;
        width: 80%;
        justify-content: center;
        gap: 10%;
        align-items: center;
        @media screen and (max-width: 768px) {
            justify-content: center;
            width: 100%;
        }
        .blob-skills {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            border-radius: 50%;
            background-color: $black;
            box-shadow: 0 0 20px rgba(157, 39, 3, 0.55);
            height: 50px;
            width: 50px;
            color: $black;
            position: relative;
            margin: 10px 0;
            @media screen and (max-width: 768px) {
                margin: 30px 0;
            }
            img {
              border-radius: 50%;
              object-fit:contain;
              height: 60px;
              width: 60px;
            }
            p {
                width: 150px;
                text-align: center;
            }
        }
        .skills-label {
            color: $white;
            position: absolute;
            bottom: -30px;
            font-size: 18px;
        }
    }
    .line {
        border-top: 1px solid $color-2;
        margin: 30px auto 10px;
        width: 80%;
    }
}